import React, {useState, useEffect } from 'react';
import {connect} from 'react-redux';
import './form.scss'
import Paper from '@material-ui/core/Paper';
import backend from '../../../services/backend'
import { Link } from "react-router-dom";
import Spinner from "../../../components/spinner";
import { Formik } from 'formik';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import SubmitIcon from '@material-ui/icons/SubdirectoryArrowRight'
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import Chip from '@material-ui/core/Chip';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';


import { toast } from 'react-toastify';
import {onError} from "../../../services/error-helpers";



export default function Form(props) {

  let initialValues = !props.data ? {} : {
    title: props.data.title || '',
    zIndex: props.data.zIndex || '',
    conflictsWith: props.data.conflictsWith || [],
    inheritColorFrom: props.data.inheritColorFrom || null,
    showOnStart: props.data.showOnStart || false
  };

  const [layersList, setLayersList] = useState([]);

  useEffect(() => {
    backend.getLayers(props.match.params.setId)
      .then(response => {
        if (!response.data || !response.data.layers) return;
        let layers = response.data.layers;
        layers = layers.filter(layer => {
          return layer._id !== props.match.params.id;
        });
        setLayersList(layers);
      })
      .catch(onError)
  }, []);

  return (
    <div className="Form">
      <h2>Basic</h2>
      <Formik
        enableReinitialize
        initialValues={initialValues}
        onSubmit={(values, { setSubmitting }) => {
          props.onSubmit(values)
            .then(response => {
              toast.success('Form was successfully saved')
            })
            .catch(onError)
            .finally(() => setSubmitting(false))
        }}
      >
        {props => {
          const {
            values,
            errors,
            touched,
            isSubmitting,
            handleChange,
            handleSubmit,
          } = props;
          return (
            <form onSubmit={handleSubmit}>
              <div>
                <TextField
                  id="title"
                  label="Title"
                  value={values.title}
                  onChange={handleChange}
                  margin="normal"
                  variant="outlined"
                  className="full-width"
                />
              </div>
              <div>
                <TextField
                  id="zIndex"
                  label="zIndex"
                  type="number"
                  value={values.zIndex}
                  onChange={handleChange}
                  margin="normal"
                  variant="outlined"
                  className="full-width"
                />
              </div>
              <div>
                <FormControlLabel
                  control={
                    <Checkbox
                      id="showOnStart"
                      name="showOnStart"
                      checked={values.showOnStart}
                      onChange={handleChange}
                      color="primary"
                    />
                  }
                  label="Show layer image on start"
                />
              </div>
              <div>
                <FormControl>
                  {/*<InputLabel htmlFor="inheritColorFrom">Have same color as</InputLabel>*/}
                  <Select
                    id="inheritColorFrom"
                    name="inheritColorFrom"
                    className="multiselect"
                    value={values.inheritColorFrom}
                    onChange={handleChange}
                    input={<Input name="age" id="age-helper" />}
                  >
                    <MenuItem value={null}>
                      None
                    </MenuItem>
                    {layersList.map(layer => (
                      <MenuItem key={layer._id} value={layer._id}>
                        {layer.title}
                      </MenuItem>
                    ))}
                  </Select>
                  <FormHelperText>Inherits colos from</FormHelperText>
                </FormControl>
              </div>

              <div>
                <FormControl>
                  {/*<InputLabel htmlFor="select-multiple-checkbox">Conflicts with</InputLabel>*/}
                  <Select
                    id="conflictsWith"
                    name="conflictsWith"
                    className="multiselect full-width"
                    multiple
                    value={values.conflictsWith}
                    onChange={handleChange}
                    input={<Input id="select-multiple-chip" />}
                    renderValue={selected => (
                      <div>
                        {selected.map(value => {
                          let layer = layersList.find(lay => lay._id === value);
                          if (!layer) return null;
                          return (
                            <Chip
                              key={value}
                              label={layer.title}
                            />
                          )
                        })}
                      </div>
                    )}
                  >
                    {layersList.map(layer => (
                      <MenuItem key={layer._id} value={layer._id}>
                        {layer.title}
                      </MenuItem>
                    ))}
                  </Select>
                  <FormHelperText>Conflicts with</FormHelperText>
                </FormControl>
              </div>
              {errors.email && touched.email && (
                <div className="input-feedback">{errors.email}</div>
              )}
              <br/>
              <br/>

              <Button variant="contained" type="submit" color="primary" disabled={isSubmitting}>
                <SubmitIcon /> Submit
              </Button>
            </form>
          );
        }}
      </Formik>
    </div>
  )
}
